import { basicAlert } from '@/helpers/SweetAlert';

export function validateError(response){
    if (response) {
        if (response.status === 400) {
            basicAlert(() => { }, 'error', 'Solicitud incorrecta', 'Los datos proporcionados no son vlidos');
        } else {
            basicAlert(() => { }, 'error', 'Error de conexin', 'Hubo un problema de conexin');
        }
    } else {
        basicAlert(() => { }, 'error', 'Error de conexin', 'Hubo un problema de conexin');
    }
}